*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  -webkit-tap-highlight-color: transparent;
}

body {
  color: var(--color-primary);
  font-family: var(--font-family);
  font-size: var(--font-size);
  letter-spacing: var(--letter-spacing);
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4 {
  margin-top: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

:focus, :active, a:focus, a:active {
  outline: none;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

address {
  font-style: normal;
}

input, button, textarea {
  font-family: var(--font-family);
  font-size: inherit;
  border: none;
  outline: none;
}

button {
  cursor: pointer;
}

:root {
  --color-primary: #000;
  --color-accent: #d41443;
  --color-light: #fff;
  --color-other: #e18298;
  --color-yellow: #f0d1a5;
  --color-green: #c2e297;
  --color-linear: #e1e1e1;
  --color-over-line: #ffa5ba;
  --color-description: #907e82;
  --color-light-bg: #fff;
  --color-circle-bg: #ffa5ba;
  --color-accent-bg: #ffb8ca;
  --color-menu-bg: #e17992;
  --color-review-bg: #fafafa;
  --color-review-bg-tab: #fdfdfd;
  --color-bg-section: #fff5f6;
  --color-accent-btn-bg: #d41443;
  --color-light-btn-bg: #fff;
  --color-accent-hover-btn-bg: #c7133e;
  --color-hover-btn-bg: #ecebeb;
  --font-family: "DM sans", sans-serif;
  --font-family-secondary: "Titan One";
  --font-size: 16px;
  --letter-spacing: .04em;
  --anim: 250ms cubic-bezier(.4, 0, .2, 1);
  --anim-slow: 500ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.button {
  height: 44px;
  background-color: var(--color-light);
  color: var(--color-accent);
  transition: background-color var(--anim);
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  column-gap: 13px;
  padding: 0 37px;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.button__icon {
  fill: var(--color-accent);
}

.button:hover, .button:focus {
  background-color: var(--color-hover-btn-bg);
}

.button--accent {
  background-color: var(--color-accent);
  color: var(--color-light);
}

.button--accent:hover, .button--accent:focus {
  background-color: var(--color-accent-hover-btn-bg);
}

.button__icon--accent {
  fill: var(--color-light);
}

.button-arrow {
  width: 18px;
  height: 18px;
  background-color: var(--color-light-btn-bg);
  transition: background-color var(--anim);
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button-arrow:hover, .button-arrow:focus {
  background-color: var(--color-accent-btn-bg);
}

.button-arrow:hover .button-arrow__icon, .button-arrow:focus .button-arrow__icon {
  fill: var(--color-light);
}

.button-arrow__icon {
  fill: var(--color-accent);
  transition: fill var(--anim);
}

@media screen and (min-width: 1200px) {
  .button-arrow {
    width: 26px;
    height: 26px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 16px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

:root {
  --gutter: 2px;
  --column: 7;
}

@media screen and (min-width: 768px) {
  :root {
    --gutter: 6px;
  }
}

@media screen and (min-width: 1200px) {
  :root {
    --gutter: 8px;
  }
}

.grid {
  max-width: 100%;
}

.grid:after {
  content: "";
  clear: both;
  display: block;
}

.grid-sizer {
  width: calc((100% - var(--gutter) * (var(--column)  - 1) ) / var(--column) );
  height: 44px;
  float: left;
}

.grid-item {
  width: calc((100% - var(--gutter) * (var(--column)  - 1) ) / var(--column) );
  height: 44px;
  float: left;
  margin-bottom: var(--gutter);
}

@media screen and (min-width: 768px) {
  .grid-item {
    width: calc((100% - var(--gutter) * (var(--column)  - 1) ) / var(--column) );
    height: 104px;
    margin-bottom: var(--gutter);
  }
}

@media screen and (min-width: 1200px) {
  .grid-item {
    width: calc((100% - var(--gutter) * (var(--column)  - 1) ) / var(--column) );
    height: 138px;
    margin-bottom: var(--gutter);
  }
}

.grid-item--width2 {
  width: calc((100% - var(--gutter) * (var(--column)  - 1) ) / var(--column) * 2 + var(--gutter) );
  height: 90px;
}

@media screen and (min-width: 768px) {
  .grid-item--width2 {
    width: calc((100% - var(--gutter) * (var(--column)  - 1) ) / var(--column) * 2 + var(--gutter) );
    height: 214px;
  }
}

@media screen and (min-width: 1200px) {
  .grid-item--width2 {
    width: calc((100% - var(--gutter) * (var(--column)  - 1) ) / var(--column) * 2 + var(--gutter) );
    height: 284px;
  }
}

.grid-item--height2 {
  height: 26px;
}

@media screen and (min-width: 768px) {
  .grid-item--height2 {
    height: 61px;
  }
}

@media screen and (min-width: 1200px) {
  .grid-item--height2 {
    height: 80px;
  }
}

.grid-item--height3 {
  height: 29px;
}

@media screen and (min-width: 768px) {
  .grid-item--height3 {
    height: 68px;
  }
}

@media screen and (min-width: 1200px) {
  .grid-item--height3 {
    height: 90px;
  }
}

.grid-item--height4 {
  height: 19px;
}

@media screen and (min-width: 768px) {
  .grid-item--height4 {
    height: 44px;
  }
}

@media screen and (min-width: 1200px) {
  .grid-item--height4 {
    height: 57px;
  }
}

.grid-item--height5 {
  height: 47px;
}

@media screen and (min-width: 768px) {
  .grid-item--height5 {
    height: 111px;
  }
}

@media screen and (min-width: 1200px) {
  .grid-item--height5 {
    height: 146px;
  }
}

.grid-item--marg {
  margin-bottom: 0;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  z-index: 100;
  transition: opacity var(--anim-slow), visibility var(--anim-slow);
  background-color: rgba(0, 0, 0, .1);
  position: fixed;
  top: 0;
  left: 0;
}

.overlay.is-shown {
  opacity: 1;
  visibility: visible;
}

.modal {
  opacity: 0;
  visibility: hidden;
  min-height: 300px;
  background-color: var(--color-bg-section);
  border: 5px solid var(--color-accent-bg);
  z-index: 200;
  background-image: linear-gradient(var(--color-light) 0%, var(--color-light) 20%, var(--color-bg-section) 20%, var(--color-bg-section) 100%);
  transition: opacity var(--anim-slow), visibility var(--anim-slow), transform var(--anim-slow);
  border-radius: 24px;
  padding: 56px 24px 40px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1.1);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 550px;
  }
}

@media screen and (max-width: 767.98px) {
  .modal {
    max-width: 450px;
    min-height: 600px;
    background-image: linear-gradient(var(--color-light) 0%, var(--color-light) 25%, var(--color-bg-section) 25%, var(--color-bg-section) 100%);
  }
}

.modal.is-shown {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%)scale(1)rotateY(360deg);
}

.modal__close {
  width: 30px;
  height: 30px;
  border: 2px solid var(--color-accent-bg);
  z-index: 300;
  transition: background-color var(--anim), color var(--anim), border-color var(--anim);
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal__close:hover, .modal__close:focus {
  background-color: var(--color-accent);
  color: #fff;
  border-color: rgba(0, 0, 0, 0);
}

.modal__close-icon {
  fill: var(--color-accent-bg);
}

.modal__button {
  margin-left: auto;
  margin-right: auto;
}

.checkbox__wrapper {
  justify-content: space-evenly;
  margin-top: 24px;
  margin-bottom: 8px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .checkbox__wrapper {
    margin-bottom: 16px;
  }
}

.checkbox__inner {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.checkbox__container {
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .checkbox__container {
    margin-bottom: 24px;
  }
}

.checkbox {
  gap: 4px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .checkbox {
    gap: 10px;
  }
}

.checkbox__input {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  position: absolute;
}

.checkbox__icon {
  width: 30px;
  height: 30px;
  outline: 2px solid var(--color-accent-bg);
  outline-offset: -2px;
  background-color: var(--color-light);
  transition: background-color var(--anim), outline-color var(--anim), transform var(--anim);
  border-radius: 8px;
  flex-shrink: 0;
  transform: scale(.8);
}

.checkbox__input:checked + .checkbox__icon {
  background-color: var(--color-accent);
  outline-color: rgba(0, 0, 0, 0);
  transform: scale(1)rotate(360deg);
}

.checkbox__content {
  fill: var(--color-light);
}

.checkbox__text {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.86;
}

