}

.scroll-top {
  z-index: 90;
  width: 40px;
  height: 40px;
  background-color: var(--color-circle-bg);
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  padding-right: 2px;
  transition: all .4s;
  animation: circle 1.5s infinite alternate-reverse;
  display: flex;
  position: fixed;
  bottom: 40px;
  right: 40px;
  transform: scale(0);
}

.scroll-top__icon {
  fill: var(--color-light);
  transform: rotate(-90deg);
}

.scroll-top--active {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

@keyframes circle {
  0% {
    box-shadow: 0 0 #ffb8ca;
  }

  100% {
    box-shadow: 0 0 0 20px rgba(255, 184, 202, .6);
  }
}

